  description1: 'മാർച്ച് മാസമാകുന്നതിനു മുമ്പു തന്നെ കേരളത്തിൽ താപനില കൂടി വരികയാണ്. സൂര്യാഘാതത്തിനുള്ള സാധ്യതകളിലേക്കാണ് അത് വിരൽ ചൂണ്ടുന്നത്. എന്താണ് സൂര്യാഘാതമെന്നും സൂര്യാഘാതം വരാതിരിക്കാൻ എന്തു ചെയ്യണമെന്നും സൂര്യാഘാതം വന്നാൽ ചെയ്യേണ്ടതെന്തൊക്കെയെന്നും അറിയാം. ( rise in temperature in kerala sunburn symptoms )',
  description2:'അന്തരീക്ഷതാപം ഒരു പരിധിയിലപ്പുറം ഉയർന്ന് ശരീരത്തിലെ താപനിയന്ത്രണ സംവിധാനങ്ങൾ തകരാറിലാകുകയും ശരീരത്തിന്റെ പല പ്രവർത്തനങ്ങളും തകരാറിലാകുകയും ചെയ്യുന്ന അവസ്ഥയാണ് സൂര്യാഘാതം. സൂര്യപ്രകാശം ഏറ്റവും കടുത്ത അവസ്ഥയിൽ രാവിലെ 11 മണി മുതൽ ഉച്ചയ്ക്ക് മൂന്നു മണി വരെയാണ് ഏറ്റവുമധികം സൂര്യാഘാത സാധ്യത. പത്തു വയസ്സിൽ താഴെയും അറുപതു വയസ്സിനു മുകളിലുമുള്ളവർക്കാണ് സൂര്യാഘാത സാധ്യത ഏറ്റവും കൂടുതൽ. ചികിത്സിച്ചില്ലെങ്കിൽ തലച്ചോറും ഹൃദയവും വൃക്കകളും പേശികളും തകരാറിലാകാനുള്ള സാധ്യതയുണ്ട്. മരണം വരെ സംഭവിക്കാം',
  description3:'ഉയർന്ന ശരീരോഷ്മാവ്, ശരീരത്തിൽ ചുവന്ന പാടുകൾ, അതികഠിനമായ തലവേദന, തലകറക്കം, ചുഴലിരോഗലക്ഷണങ്ങൾ, ഉയർന്ന നാഡിമിടിപ്പ്, ബോധക്ഷയം, ഓക്കാനം, പേശിമുറുകൽ, അമിതമായ ദാഹം, കുഴഞ്ഞുവീഴൽ, അതികഠിനമായ തളർച്ച, ശരീരത്തിൽ പൊള്ളലേറ്റ പോലുള്ള കുമിളകൾ എന്നിവയൊക്കെയാണ് സൂര്യാഘാതത്തിന്റെ ലക്ഷണങ്ങൾ.സൂര്യാഘാതമേറ്റ വ്യക്തിയെ ഉടനടി തന്നെ തണലിലേക്ക് മാറ്റണം. വെള്ളം ധാരാളം നൽകണം. ഇളനീര്, നാരങ്ങാവെള്ളം എന്നിവയും നൽകാം. ഇറുകിയ വേഷങ്ങളാണെങ്കിൽ അവ അയച്ചിടണം, ശരീരത്തിൽ വെള്ളം തളിക്കണം, നനഞ്ഞ തുണി ശരീരത്തിലിടാം. ശരീരം തണുപ്പിക്കാൻ ഐസും ഉപയോഗിക്കാം. ബോധം പോയ അവസ്ഥയിൽ വെള്ളം കൊടുക്കാൻ ശ്രമിക്കരുത്. ആളെ ഇടതുവശത്തേക്ക് ചെരിച്ചു കിടത്തുകയാണ് നല്ലത്.',
