  description1: 'കൂടത്തായി കൊലപാതകവുമായി ബന്ധപ്പെട്ട് ഫ്ളവേഴ്സ് സംപ്രേഷണം ചെയ്യുന്ന കൂടത്തായി ദ ഗയിം ഓഫ് ഡത്ത് എന്ന പരമ്പര തടയണമെന്ന ഹർജി കോടതി തള്ളി. കോഴിക്കോട് അഡീഷനൽ സെഷൻസ് കോടതിയുടേതാണ് നടപടി. രണ്ടാം പ്രതി എം എസ് മാത്യുവായിരുന്നു പരമ്പരക്കെതിരെ കോടതിയെ സമീപിച്ചത്.',
