  description3:'എഴുത്തുകാരൻ്റെ സ്വതന്ത്ര്യം ലംഘിക്കരുതെന്ന മുൻ കോടതി വിധികളും കൂടി പരിഗണിച്ചാണ് കോഴിക്കോട് അഡീഷനൽ സെഷൻസ് കോടതി ഹർജി തള്ളിയത്. സീരിയലിനെതിരെ നേരത്തേ ഹൈക്കോടതിയിൽ സമർപ്പിച്ച ഹർജിയിലും ഫ്ളവേഴ്സിന് അനുകൂലമായി വിധി വന്നിരുന്നു.',
